.subscription {
  padding-top: 82px;
  padding-bottom: 109px;

  background-color: $grey-light;
  // .subscription__container
  @include _768 {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &__container {
    text-align: center;
    display: flex;
    flex-direction: column;
  }

  // .subscription__title

  &__title {
    max-width: 100%;
  }

  // .subscription__desc

  &__desc {
    max-width: 373px;
    margin: 0 auto;
    padding-top: 19px;
    padding-bottom: 30px;
  }

  // .subscription__form

  &__form {
    width: 420px;
    padding: 0 10px;
    height: 48px;
    display: flex;
    margin: 0 auto;
    @include _600 {
      width: 70%;
      height: 30px;
    }
    @include _480 {
      width: 90%;
      height: 30px;
    }
  }

  // .subscription__form-field

  &__form-field {
    border: 1px solid #242424;
    width: 338px;
    height: 100%;
    width: 100%;
    padding-left: 18px;
    font-size: 1.6rem;
    &::placeholder {
      font-size: 1.6rem;
      @include _600 {
        font-size: 1.2rem;
      }
    }
  }
  // .subscription__form-btn

  &__form-btn {
    width: 74px;
    height: 100%;
    border: 1px solid #242424;
    margin: 0;
    padding: 0;
    border-left: none;
    font-size: 2.4rem;
    line-height: 24px;
    background-color: $white;
    cursor: pointer;
    transition: all linear 0.2s;
    &:hover {
      background-color: $white-second;
    }
    @include _600 {
      width: 54px;
      font-size: 2rem;
    }
  }
}
