.burger-mobile {
  width: 24px;
  height: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2000;
  cursor: pointer;
  top: -20px;
  left: 40px;
  display: none;

  @include _979 {
    display: flex;
    height: 24px;
  }

  span {
    display: block;
    background-color: $dark;
    width: 100%;
    height: 4px;
  }
  &::before {
    content: "";
    width: 100%;
    background-color: $dark;
    height: 4px;
      }
  &::after {
    content: "";
    width: 100%;
    background-color: $dark;
    height: 4px;
  }
}

.burger-exit {
  width: 34px;
  position: relative;
  span {
    transform: rotate(45deg);
    top: 0;
    position: absolute;
  }
  &::before {
    content: "";
    display: none;
  }
  &::after {
    content: "";
    top: 0;
    transform: rotate(-45deg);
    position: absolute;
  }
}
