.catalog {
  // .catalog__container

  &__container {
    padding-top: 115px;
    padding-bottom: 115px;

    @include _768 {
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }
  .catalog__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      "mens womens"
      "obj obj";
    grid-row-gap: 70px;
    grid-column-gap: 120px;
    width: 100%;
    @include _768 {
      grid-column-gap: 40px;
    }
    @include _480 {
      grid-column-gap: 15px;
    }
  }
  &__title {
    max-width: 630px;
    font-size: 3rem;
    line-height: 36px;
    margin: 0 auto;

    padding-bottom: 90px;
  }
  // .catalog__item

  &__item {
    display: block;
    position: relative;
    display: flex;
    padding-bottom: 30px;
    grid-template-columns: 522px 522px;
    img {
      max-width: 100%;
      height: auto;
    }
  }

  // .catalog__item-mens

  &__item-mens {
    display: flex;
    grid-area: mens;
  }

  // .catalog__item-img

  &__item-img {
  }

  // .catalog__item-link

  &__item-link {
    bottom: 0;
    left: 0;
    position: absolute;
  }

  // .catalog__item-womens

  &__item-womens {
    grid-area: womens;
    display: flex;
    justify-content: flex-end;

    .catalog__item-link {
    }
  }

  // .catalog__item-objects

  &__item-objects {
    grid-area: obj;
  }
}
