.shop {
  padding-top: 127px;
  padding-bottom: 127px;
  background-color: $grey-light;

  @include _768 {
    padding-top: 70px;
    padding-bottom: 70px;
  }
  // .shop__container

  &__container {
    background-color: $white-second;
    padding: 0;
    display: flex;
    justify-content: space-between;
    height: max-content;
    align-items: center;

    @include _979 {
      flex-direction: column;
    }
  }

  // .shop__content

  &__content {
    padding-left: 93px;
    padding-top: 90px;
    width: 35%;
    padding-right: 104px;
    @include _979 {
      display: block;
      width: 100%;
      padding-bottom: 40px;
    }
    @include _480 {
      padding: 20px;
    }
  }

  // .shop__preview

  &__preview {
    width: 65%;
    @include _979 {
      width: 100%;
    }
  }

  // .shop__img

  &__img {
    width: 100%;
  }
}
