.footer {
  padding: 85px 10px;
  // .footer__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  // .footer__nav

  &__nav {
    @include _600 {
      width: 50%;
      margin-bottom: 20px;
      display: inline-block;
    }
  }

  // .footer__nav-list

  &__nav-list {
  }

  // .footer__nav-item

  &__nav-item {
    margin-bottom: 16px;
  }

  // .footer__nav-link

  &__nav-link {
    font-size: 1.5rem;
    line-height: 18px;
    transition: all linear 0.2s;
    &:hover {
      color: $grey-light;
    }
  }

  // .footer__right

  &__right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-bottom: 66px;
    @include _600 {
      width: 100%;
      flex-direction: row-reverse;
      align-items: center;
      justify-content: space-between;
    }
    @include _480 {
      flex-direction: column-reverse;
      padding-bottom: 0px;
    }
  }

  // .footer__right-content

  &__right-content {
    display: flex;
  }

  // .footer__right-content-text

  &__right-content-text {
    width: 100%;
    max-width: 240px;
    text-align: end;
    padding-right: 24px;
    p {
      font-size: 1.5rem;
      line-height: 21px;
    }
    @include _480 {
      width: 100%;
      text-align: center;
    }
  }

  // .footer__icon

  &__icon {
    @include _768 {
      width: 40px;
      height: auto;
    }
    @include _480 {
      display: none;
    }
  }
}

.social-btns {
  max-width: 122px;
  height: 30px;
  display: flex;
  justify-content: space-between;
  @include _600 {
    margin-right: 20px;
  }
}
.social-link {
  margin-top: 30px;
  padding-right: 16px;
  @include _600 {
    margin-top: 0;
  }
}
.social-link--instagram {
}
.social-icons {
  @include _480 {
    width: 20px;
    height: auto;
  }
}
