*,
*::before,
*::after {
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.--dir-col {
  flex-direction: column;
}

html {
  font-size: 10px;
  @include _480 {
    font-size: 8px;
  }
  @include _320 {
    font-size: 6px;
  }
}

html,
body {
  min-height: 100%;
  position: relative;
  font-family: "Roboto", sans-serif;
}

body {
  color: $dark;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

li {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: inherit;
}
ul {
  padding: 0;
}
