.container {
  @include container("1180px", "10px");
}

.section__title {
  font-weight: 400;
  max-width: 208px;
  font-size: 38px;
  line-height: 46px;

  @include _979 {
    font-size: 30px;
  }
  @include _480 {
    max-width: 100%;
    font-size: 22px;
  }
}
.section__desc {
  font-size: 1.5rem;
  line-height: 21px;
  margin-top: 32px;
}
.section__link {
  display: block;
  color: $dark;
  font-size: 1.4rem;
  line-height: 21px;
  margin-top: 32px;
  text-decoration: underline;
  transition: color linear 0.2s;
  &:hover {
    color: lighten($dark, 20%);
  }
}

.none {
  display: none;
}
.absolute {
  position: absolute;
}
