.story {
  min-height: 600px;
  background-image: url("../../images/story-bg.jpg");
  @include bg("cover", "center");
  @include _480 {
    background-position: 40% 50%;
  }
  // .story__container
  &__container {
    text-align: center;
  }

  // .story__title

  &__title {
    color: $white;
    max-width: 782px;
    margin: 0 auto;
    padding-top: 237px;
    font-size: 35px;

    @include _979 {
      font-size: 20px;
    }
  }

  // .story__link

  &__link {
    color: $white;
  }
}
