.header {
  width: 100%;
  position: relative;
}
.header__top {
  width: 100%;
  background-color: $grey-light;
}
.header__top-wrapper {
  height: 45px;
  max-width: max-content;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding: 9px 0;
}
.header__top-title {
  font-size: 1.4rem;
  line-height: 20px;
}
.header__top-btn {
  padding: 9px 20px;
  margin-left: 16px;
  @include _480 {
    padding: 5px 10px;
  }
}
.header__inner {
  display: flex;
  justify-content: space-between;
  height: 120px;
  align-items: center;
}
.header__left {
  display: flex;
  align-items: center;
}
.header__logo {
  margin-right: 50px;
  @include _600 {
    width: 132px;
  }
  @include _320 {
    width: 80px;
  }
}
.header__menu {
  @include _979 {
    display: none;
  }
}
.header__menu-list {
  display: flex;
}
.header__menu-item {
  margin-right: 32px;
  &:last-child {
    margin-right: 0;
  }
}
.header__menu-link {
  font-size: 1.7rem;
  line-height: 20px;
  transition: all linear 0.2s;
  &:hover {
    color: $grey-light;
  }
}
.header__right {
  display: flex;
  align-items: center;
  span {
    width: 1px;
    height: 20px;
    background-color: #000;
    margin-right: 12px;
    @include _979 {
      display: none;
    }
  }
  @include _979 {
    .header__right-link,
    .login-link {
      display: none;
    }
  }
}
.header__right-link {
  margin-right: 13px;
  font-size: 1.7rem;
  transition: all linear 0.2s;
  &:hover {
    color: $grey-light;
  }
}
.hero__link {
  max-width: max-content;
}
.login-link {
  margin-left: 10px;
}
.header__card {
  cursor: pointer;
}
.header__search {
  display: none;
  width: 18px;
  height: 18px;
  margin-right: 10px;
  @include _979 {
    display: block;
  }
}
.container {
}
.btn {
  border: none;
  background-color: $dark;
  color: $white-second;
}
