.hero {
  width: 100%;
  height: 600px;
  background-image: url("../images/hero-bg.jpg");
  @include bg("cover", "center");
}

.hero__container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: #fff;
  padding-bottom: 10px;
}
.hero__title {
  font-size: 1.8rem;
  line-height: 28px;
  font-weight: 400;
}
.hero__link {
  line-height: 22px;
  color: #fff;
  font-size: 1.5rem;
  text-decoration: underline;
}
