.products {
  padding-bottom: 110px;
}
.products__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.products__categories-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.products__categories {
  padding: 40px 0;
  padding-bottom: 20px;
}
.products__categories-list {
  width: 800px;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;

  @include _480 {
    width: 600px;
  }
}
.products__categories-item {
  display: flex;

  color: #b3b3b3;
  cursor: pointer;
  font-size: 1.7rem;
  line-height: 27px;
  padding-bottom: -1px;
  transition: all linear 0.4s;
  &:hover {
    color: #1a1a1a;
  }
  &--active {
    color: #1a1a1a;
    border-bottom: 1px solid #1a1a1a;
  }
  &:last-child {
    margin-right: 0;
  }
}

.products__list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  margin-bottom: 65px;
  @include _1125 {
    grid-template-columns: repeat(3, 1fr);
  }
  @include _979 {
    grid-template-columns: repeat(2, 1fr);
  }
}
.product__item {
  position: relative;
  display: block;
}

.product__item-link {
  display: block;

  position: relative;
}

.product__item-img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
  @include _979 {
    width: 100%;
  }
}

.product__item-focus-img {
  position: absolute;
  top: 0;
  @include _979 {
  }
}

.product__item-focus-img {
  position: absolute;
  opacity: 0;
  transition: opacity linear 0.4s;
  &:hover {
    opacity: 1;
  }
}
.product__content {
  padding-top: 20px;
}
.product__item-title {
  font-size: 1.5rem;
  line-height: 18px;
}
.product__item-price {
  display: flex;
  color: $grey;
  font-size: 1.3rem;
  padding-top: 5px;
  text-transform: uppercase;
}
.product__item-old-price {
  text-transform: line-through;
}
.product__item-price--red {
  color: $red;
  padding-left: 5px;
}

.btn {
  cursor: pointer;
}
.products__btn {
  width: max-content;
  font-size: 2rem;

  line-height: 56px;
  padding: 0 61px;

  @include _768 {
    line-height: 45px;
    padding: 0 30px;
  }
}

*::-webkit-scrollbar-button {
  background-image: url("");
  background-repeat: no-repeat;
  width: 6px;
  height: 0px;
}

*::-webkit-scrollbar-track {
  background-color: #32312e;
  box-shadow: 0px 0px 3px #000 inset;
}

*::-webkit-scrollbar-thumb {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #ffcb17;
  box-shadow: 0px 1px 1px #fff inset;
  background-image: url("https://yraaa.ru/_pu/24/59610063.png");
  background-position: center;
  background-repeat: no-repeat;
}

*::-webkit-resizer {
  background-image: url("");
  background-repeat: no-repeat;
  width: 7px;
  height: 0px;
}

*::-webkit-scrollbar {
  width: 11px;
}
