.mobile-navigation-wrapper {
  z-index: 100;
  position: absolute;
  background-color: $white-second;
  top: 0;
  width: 50%;
  height: 100vh;
  display: none;
  padding-top: 130px;
  @include _480 {
    width: 100%;
  }
}
.mobile-navigation-list {
}
.mobile-navigation-item {
  padding: 10px;
  width: 100%;
  border-bottom: 1px solid $grey-light;
  font-size: 17px;
}
.mobile-navigation-link {
}
.mobile-visiable {
  display: block;
}
